.todoListMain .header input {
    padding: 10px;
    font-size: 16px;
    border: 2px solid #0066FF;
    width: 165px;
  }
  .todoListMain .header button {
    padding: 10px;
    font-size: 16px;
    margin: 10px;
    margin-right: 0px;
    background-color: rgba(0,102,255,.5);
    color: #FFF;
    border: 2px solid rgba(0,102,255,.5);
  }
  .todoListMain .header button:hover {
    background-color: #003399;
    border: 2px solid #003399;
    cursor: pointer;
  }
  .todoListMain .theList {
    list-style: none;
    padding-left: 0;
    width: 20em;
    margin: 0 auto 1rem;
  } 
  .todoListMain .theList li {
    color: #fff;
    background-color: rgba(0,102,255,.5);
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    text-align: center;
    transition: background-color .2s ease-out;
  }
   
  .todoListMain .theList li:hover {
    background-color: rgba(255,102,0, 0.5);
    cursor: pointer;
  }
